
import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import * as firebase from 'firebase/app';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { take } from 'rxjs/operators';

@Injectable()
export class CommentService implements Resolve<any> {

    comments: any[];
    onCommentsChanged: BehaviorSubject<any> = new BehaviorSubject({});

    constructor( private afs: AngularFirestore ) { }

    /**
     * Resolve
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any {
        return this.getComments();
    }

    getComments(): Promise<any> {
        return new Promise((resolve, reject) => {
          this.afs.collection('messages', res => res.orderBy('time')).snapshotChanges().map(changes => {
            return changes.map(c => ({id: c.payload.doc.id, ...c.payload.doc.data()}));
          }).map(res => {
              return res.reverse().map((item: any) => {
                  if (item.user) {
                    item.userData = this.afs.doc(`users/${item.user}`).valueChanges();
                  }

                  return item;
              });
          }).subscribe(comments => {
            this.comments = comments;
            this.onCommentsChanged.next(this.comments);
            resolve(comments);
          }, reject);
        });
    }

    approveComment(comment: any) {
        return this.afs.doc(`messages/${comment.id}`).update({
            status: 'allow',
            reason: firebase.firestore.FieldValue.delete()
        });
    }

    deleteComment(comment: any) {
        return this.afs.doc(`messages/${comment.id}`).delete();
    }

    deleteCommentAndBlockUser(comment: any) {
        return Promise.all([
            this.afs.doc(`users/${comment.user}`).update({
                isBlocked: true,
                block: {
                    comment: comment.msg,
                    reason: comment.reason || null 
                }
            }),
            this.afs.doc(`messages/${comment.id}`).delete(),
            this.deleteCol(`messages/${comment.id}/readed`)
        ]);
    }

    private async deleteCol(path: string) {
        const batch = this.afs.firestore.batch();
        const qs = await this.afs.collection(path).ref.get();
        qs.forEach(doc => batch.delete(doc.ref));
        return batch.commit();
    }

}



