import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FuseSharedModule } from '@fuse/shared.module';
import { CdkTableModule } from '@angular/cdk/table';

import { 
  MatButtonModule, 
  MatChipsModule, 
  MatFormFieldModule, 
  MatIconModule, 
  MatInputModule, 
  MatPaginatorModule, 
  MatRippleModule, 
  MatSelectModule, 
  MatSnackBarModule,
  MatSortModule, 
  MatTableModule, 
  MatTabsModule, 
  MatCheckboxModule,
  MatMenuModule} from '@angular/material';

import { CommentsComponent } from './comments.component';
import { CommentService } from './comment.service';
import { CommonModule } from '@angular/common';

const routes = [
  {
      path     : '',
      component: CommentsComponent,
      resolve  : {
        data: CommentService
    }
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
    FuseSharedModule,
    CdkTableModule,
    MatButtonModule,
    MatCheckboxModule,
    MatChipsModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSelectModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
  ],
  declarations: [
    CommentsComponent
  ],
  providers: [
    CommentService
  ]
})
export class CommentsModule { }



