import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSnackBar, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { SelectionModel } from '@angular/cdk/collections';

import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { fuseAnimations } from '@fuse/animations';
import { FuseUtils } from '@fuse/utils';

import { CommentService } from './comment.service';

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.scss'],
  animations : fuseAnimations
})
export class CommentsComponent implements OnInit {

  selection = new SelectionModel<string>(true, []);
  
  dataSource: CommentsDataSource | null;
  displayedColumns = ['checkbox', 'comments', 'category', 'date', 'user'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild('filter') filter: ElementRef;
  @ViewChild(MatSort) sort: MatSort;

  categories: string[];
  selectedCategory: string;

  constructor( private commentService: CommentService, private snackBar: MatSnackBar ) { }

  ngOnInit() {
    this.categories = [
        'new',
        'allow',
        'Spam',
        'Hateful',
        'Violent/Abusive',
        'Child Abuse',
        'Sexually offensive',
        'All'
    ];

    this.dataSource = new CommentsDataSource(this.commentService, this.paginator, this.sort);
    Observable.fromEvent(this.filter.nativeElement, 'keyup')
            .debounceTime(150)
            .distinctUntilChanged()
            .subscribe(() => {
                if ( !this.dataSource ) {
                    return;
                }
                this.dataSource.filter = this.filter.nativeElement.value;
            });
  }

  filterByCategory(category: string) {
    this.selectedCategory = category;
    this.dataSource.category = (category === 'All') ? '' : category;

    setTimeout(() => {
        if (!this.dataSource.filteredData || !this.dataSource.filteredData.length) {
            this.selection.clear();
        }
    });
  }

  isAllSelected(): boolean {
    if (!this.dataSource) { 
        return false; 
    }
    
    if (this.selection.isEmpty()) { 
        return false; 
    }

    if (this.filter.nativeElement.value) {
      return this.selection.selected.length === this.dataSource.filteredData.length;
    } else {
      return this.selection.selected.length === this.dataSource.filteredData.length;
    }
  }

  masterToggle() {
    if (!this.dataSource) { 
        return; 
    }

    if (this.isAllSelected()) {
      this.selection.clear();
    } else if (this.filter.nativeElement.value) {
      this.dataSource.filteredData.forEach(data => this.selection.select(data));
    } else {
      this.dataSource.filteredData.forEach(data => this.selection.select(data));
    }
  }

  approveSelected() {
      this.selection.selected.forEach(comment => {
          this.commentService.approveComment(comment).then(() => {
            this.selection.clear();
            this.snackBar.open('Operation completed.', null,  {duration: 500});
          });
      });
  }

  deleteSelected() {
    this.selection.selected.forEach(comment => {
        this.commentService.deleteComment(comment).then(() => {
            this.selection.clear();
            this.snackBar.open('Operation completed.', null,  {duration: 500});
        });
    });
  }

  deleteAndBlockSelected() {
    this.selection.selected.forEach(comment => {
        this.commentService.deleteCommentAndBlockUser(comment).then(() => {
            this.selection.clear();
            this.snackBar.open('Operation completed.', null,  {duration: 500});
        });
    });
  }
}

export class CommentsDataSource extends DataSource<any> {
  _filterChange = new BehaviorSubject('');
  _filteredDataChange = new BehaviorSubject('');
  _categoryChange = new BehaviorSubject('');

  get filteredData(): any {
      return this._filteredDataChange.value;
  }

  set filteredData(value: any) {
      this._filteredDataChange.next(value);
  }

  get filter(): string {
      return this._filterChange.value;
  }

  set filter(filter: string) {
      this._filterChange.next(filter);
  }

  get category(): string {
    return this._categoryChange.value;
  }

  set category(category: string) {
      this._categoryChange.next(category);
  }

  constructor(
      private commentService: CommentService,
      private _paginator: MatPaginator,
      private _sort: MatSort
  ) {
      super();
      this.filteredData = this.commentService.comments;
  }

  connect(): Observable<any[]> {
      const displayDataChanges = [
          this.commentService.onCommentsChanged,
          this._paginator.page,
          this._filterChange,
          this._sort.sortChange,
          this._categoryChange
      ];

      return Observable.merge(...displayDataChanges).map(() => {
          let data = this.commentService.comments.slice();
         
          data = this.filterDataByCategory(data, this.category);

          data = this.filterData(data);

          this.filteredData = [...data];

          data = this.sortData(data);


          // Grab the page's slice of data.
          const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
          return data.splice(startIndex, this._paginator.pageSize);
      });
  }

  filterData(data) {
      if ( !this.filter ) {
          return data;
      }
      return FuseUtils.filterArrayByString(data, this.filter, 'userData');
  }

  filterDataByCategory(data: any[], category: string) {
    if (!this.category) {
        return data;
    }

    return data.filter(item => {
        if (item.reason && item.reason === category) {
            return true;
        }
        return item.status === category;
    });
  }

  sortData(data): any[] {
    if ( !this._sort.active || this._sort.direction === '' )
    {
        return data;
    }

    return data.sort((a, b) => {
        let propertyA: number | string = '';
        let propertyB: number | string = '';

        switch ( this._sort.active )
        {
            case 'id':
                [propertyA, propertyB] = [a.id, b.id];
                break;
            case 'name':
                [propertyA, propertyB] = [a.name, b.name];
                break;
            case 'categories':
                [propertyA, propertyB] = [a.categories[0], b.categories[0]];
                break;
            case 'price':
                [propertyA, propertyB] = [a.priceTaxIncl, b.priceTaxIncl];
                break;
            case 'quantity':
                [propertyA, propertyB] = [a.quantity, b.quantity];
                break;
            case 'active':
                [propertyA, propertyB] = [a.active, b.active];
                break;
        }

        const valueA = isNaN(+propertyA) ? propertyA : +propertyA;
        const valueB = isNaN(+propertyB) ? propertyB : +propertyB;

        return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
    });
  }

  disconnect() {
  }
}



